@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.leiden-transcription hi[rend="superscript"] {
    vertical-align: super;
  }
  
  .leiden-transcription note { 
    visibility: hidden;
  }
  
  .leiden-transcription { 
    counter-reset: footnotecounter;
    line-height: 1.5; 
  }
  
  .leiden-transcription note:after { 
     
   /* content: url('../images/glyphicons-152-new-window.png'); */
     /*glyphicon-share-alt; */
   /*  background-color: rgba(0,255,0,0.3); */
    
    visibility: visible;
  
    counter-increment: footnotecounter; /* Add 1 to footnote */
    content: counter(footnotecounter);
    font-size: smaller; 
    margin-left: .1em;
    vertical-align: super; /* sets the footnote above the baseline */
  }
  
  /* add a line break after lb */
  .epidoc-transcription lb, .leiden-transcription lb {
      display:inline;
  }
  .epidoc-transcription lb:not(:first-child):before, .leiden-transcription lb:not(:first-child):before {
      content:"\a";
      white-space: pre;
  }
  /* add line numbers */
  .epidoc-transcription, lb:after {
      content: attr(n) '. ';
      width: 2em; 
      display: block; 
      float: left; 
      margin-left: 1em; 
  }
